import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatchesService } from 'src/app/services/matches.service';
import { PlayerService } from 'src/app/services/player.service';
import { Location } from '@angular/common'

@Component({
  selector: 'app-join',
  templateUrl: './join.component.html',
  styleUrls: ['./join.component.scss']
})
export class JoinComponent implements OnInit {

  public matches: any[] = [];

  public playername: string = '';
  public matchname: string = '';
  public error: string = '';

  public creating: boolean = false;
  public joining: boolean = false;
  public observer: boolean = false;

  constructor(
    private matchesService: MatchesService,
    private playerService: PlayerService,
    private location: Location,
    private router: Router) { }

  async ngOnInit() {
    this.playername = await this.playerService.getName();
  }

  back(): void {
    this.location.back()
  }

  async joinMatch() {
    const result = await this.matchesService.getMatch(this.matchname);
    if (result) {
      this.playerService.isObserver = this.observer;
      this.router.navigate([`game`], { queryParams: { match: this.matchname } })
    } else {
      this.error = "Partita non esistente"
    }
  }

  changeName() {
    this.playerService.setName(this.playername);
  }

}
