import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PlayerService {

  private static playerStorageKey : string = 'playerName';

  private playerName: string = '';

  public isObserver: boolean = false;

  constructor() {
    const pname = localStorage.getItem(PlayerService.playerStorageKey);
    if (pname) {
      this.playerName = pname;
    }
  }

  setName(name: string) {
    this.playerName = name;
    localStorage.setItem(PlayerService.playerStorageKey, name)
  }

  getName() {
    return this.playerName;
  }
}
