import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { lexicographicSortSchema } from 'graphql';
import { first } from 'rxjs/operators';
import { Match, Player, Round, Score } from 'src/app/models/match';
import { MatchesService } from 'src/app/services/matches.service';
import { PlayerService } from 'src/app/services/player.service';
import { RoundService } from 'src/app/services/round.service';
import { parseISO, differenceInSeconds } from 'date-fns'
import { fadeInOnEnterAnimation, fadeOutOnLeaveAnimation } from 'angular-animations';
import { Location } from '@angular/common'

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.scss'],
  animations: [
    fadeInOnEnterAnimation(),
    fadeOutOnLeaveAnimation()
  ]
})
export class GameComponent implements OnInit, OnDestroy {

  public match: Match = { id: '', name: '', players: [], playerNumber: 0, roundNumber: 0, rounds: [] }; //TODO init with null?
  public playerName: string = '';
  public matchName: string = '';

  public message: string = '';

  public currentPlayer: Player;
  public currentRound: Round;

  public isSpectator = false;

  public roundTime: any = 0;


  public leaderboard: Score[] = [];


  public waitingForPlayers: boolean = true;
  public playing = true;

  //DEBUG
  public word: string = 'tears';

  public currentWord: string = '';


  private lastMatchJson = '';
  private timerInterval = 2000;
  private timerBasicIntervalMs = 2000;
  private timerIntervalMultiplier = 1.2;



  constructor(
    private matchesService: MatchesService,
    private playerService: PlayerService,
    private roundService: RoundService,
    private router: Router,
    private location: Location,
    private route: ActivatedRoute
  ) { }

  async ngOnInit() {

    const params: ParamMap = await this.route.queryParamMap.pipe(first()).toPromise() as ParamMap;
    this.matchName = params.get('match') ? params.get('match') as string : '';
    if (this.matchName) {
      this.match = await this.matchesService.getMatch(this.matchName);
      if (this.playerService.isObserver) {
        this.isSpectator = true;
      } else {
        this.playerName = this.playerService.getName();
        if (this.match.players.findIndex(x => x.name == this.playerName) < 0) {
          if (this.match.playerNumber > this.match.players.length) {
            await this.matchesService.addPlayerToMatch(this.matchName, this.playerName);
            this.isSpectator = false;
          } else {
            this.isSpectator = true;
          }

        }
      }

      this.refresh();
    }
    else {
      //TODO ERROR
      this.word = params.get('word') as string;
    }

    this.autoRefresh();

    setInterval(() => {
      if (this.currentRound?.startTime) {
        this.roundTime = differenceInSeconds(new Date(), parseISO(this.currentRound.startTime));
      }
    })
  }

  back(): void {
    this.location.back()
  }

  autoRefresh() {
    // const matchJson = JSON.stringify(match);
    // if (this.lastMatchJson == matchJson) {
    //   this.timerInterval *= this.timerIntervalMultiplier;
    // }
    // else {
    //   this.timerInterval = this.timerBasicIntervalMs;
    // }
    // this.lastMatchJson = matchJson;

    setTimeout(() => {
      if (this.playing) {
        this.refresh();
      }
      this.autoRefresh();
    }, this.timerInterval);
  }



  async refresh() {
    const match = await this.matchesService.getMatch(this.matchName);
    this.updateValues(match);
  }

  updateValues(newMatch: Match) {
    this.match = newMatch;
    this.waitingForPlayers = this.isWaitingForPlayers();

    this.playing = this.isStillPlaying();

    this.currentRound = this.roundService.getCurrentRound(this.match) as Round; // TODO possibile eerore a fine match
    this.currentPlayer = this.match.players.find(player => player.name == this.playerName) as Player;

    this.leaderboard = this.roundService.getMatchLeaderboard(this.match);

  }

  isWaitingForPlayers(): boolean {
    return this.match.players.length < this.match.playerNumber;
  }

  isStillPlaying(): boolean {
    if (this.waitingForPlayers) {
      return true;
    }
    let res = true;
    this.match.rounds.forEach(round => {
      let everyPlayerTerminate = true;
      this.match.players.forEach(player => {
        everyPlayerTerminate = everyPlayerTerminate && !!round.answers.find(answ => answ.complete && answ.player.name == player.name)
      });
      res = res && !everyPlayerTerminate;
    })
    return res;
  }

  ngOnDestroy(): void {
    this.playing = false;
  }


  public addLetter(letter: string) {
    if (this.currentWord.length < this.currentRound.word.length) {
      this.currentWord += letter;
    }
  }

  public deleteLetter() {
    if (this.currentWord.length > 0) {
      this.currentWord = this.currentWord.slice(0, this.currentWord.length - 1);
    }

  }

  public async sendAnswer() {
    this.message = '';
    if (this.currentWord.length < this.currentRound.word.length) {
      this.message = "Parola troppo corta";
    }
    if (!this.roundService.isWordAcceptable(this.currentRound, this.currentWord)) {
      this.message = "Parola non valida";
    } else {
      await this.roundService.sendAnswer(this.currentRound, this.currentWord, this.currentPlayer);
      this.currentWord = '';
      this.refresh();
    }

  }

}
