import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Player, Round } from 'src/app/models/match';
import { pulseAnimation } from 'angular-animations';

export interface BoardKey {
  label: string,
  icon?: string,
  action: string,
  class?: string,
  valueClass?: string,
  animate?: boolean
}

@Component({
  selector: 'app-keyboard',
  templateUrl: './keyboard.component.html',
  styleUrls: ['./keyboard.component.scss'],
  animations: [
    pulseAnimation({scale: 1.2, direction: '=>', duration: 200})
  ]
})

export class KeyboardComponent implements OnInit {


  @Output('letter') letter: EventEmitter<string> = new EventEmitter();
  @Output('delete') delete: EventEmitter<null> = new EventEmitter();
  @Output('send') send: EventEmitter<null> = new EventEmitter();

  public actualKey: BoardKey[][] = [[]];

  private keyEn: BoardKey[][] = [
    [
      {
        label: 'q',
        action: 'letter'
      },
      {
        label: 'w',
        action: 'letter'
      },
      {
        label: 'e',
        action: 'letter'
      },
      {
        label: 'r',
        action: 'letter'
      },
      {
        label: 't',
        action: 'letter'
      },
      {
        label: 'y',
        action: 'letter'
      },
      {
        label: 'u',
        action: 'letter'
      },
      {
        label: 'i',
        action: 'letter'
      },
      {
        label: 'o',
        action: 'letter'
      },
      {
        label: 'p',
        action: 'letter'
      }
    ],
    [
      {
        label: 'a',
        action: 'letter',
        class: 'letter-space'
      },
      {
        label: 's',
        action: 'letter'
      },
      {
        label: 'd',
        action: 'letter'
      },
      {
        label: 'f',
        action: 'letter'
      },
      {
        label: 'g',
        action: 'letter'
      },
      {
        label: 'h',
        action: 'letter'
      },
      {
        label: 'j',
        action: 'letter'
      },
      {
        label: 'k',
        action: 'letter'
      },
      {
        label: 'l',
        action: 'letter'
      }
    ],
    [
      {
        label: '',
        icon: 'pi-backward',
        action: 'delete',
        class: 'letter-size2'
      },
      {
        label: 'z',
        action: 'letter',
        class: 'letter'
      },
      {
        label: 'x',
        action: 'letter'
      },
      {
        label: 'c',
        action: 'letter'
      },
      {
        label: 'v',
        action: 'letter'
      },
      {
        label: 'b',
        action: 'letter'
      },
      {
        label: 'n',
        action: 'letter'
      },
      {
        label: 'm',
        action: 'letter'

      },
      {
        label: '',
        icon: 'pi-reply',
        action: 'send',
        class: 'letter-size2'
      }

    ]
  ]

  public round: Round;
  public player: Player;
  @Input('round') set setRound(value: Round) {
    this.round = value;
    this.update();
  };
  @Input('player')set setPlayer(value: Player) {
    this.player = value;
    this.update();
  };

  constructor() { 
  }

  ngOnInit(): void {
  }

  action(key:BoardKey) {
    key.animate = true;
    // setTimeout(()=>{key.animate = false;},300);
    switch (key.action) {
      case 'letter':
        this.letter.emit(key.label);
        break;
      case 'delete':
        this.delete.emit(null);
        break;
      case 'send':
        this.send.emit(null);
        break;
    }
  }

  update() {
    const type = this.round && this.round.type ? this.round.type : '';
    switch (type) {
      default:
        this.actualKey = JSON.parse(JSON.stringify(this.keyEn));
    }
    const word = this.round && this.round.word ? this.round.word : '';
    const answers = this.round && this.round.answers ?  this.round.answers.filter(x => x.player.name == this.player.name) : [];
    answers.forEach(answer => {
      for (let i = 0; i < answer.value.length; i++) {
        const letter = answer.value[i];
        if (word[i] == letter) { this.setLetter(letter, 1); }
        else if (word.includes(letter)) { this.setLetter(letter, -1); }
        else { this.setLetter(letter, 0); }
      }
    })
  }

  setLetter(letter: string, value: number) {
    this.actualKey.forEach(line => {
      line.filter((x: BoardKey) => x.label == letter).forEach((key: BoardKey) => {
        key.valueClass = value == 0 ? 'gray' : value == -1 ? 'yellow' : 'green';
      })
    })
  }

}
