import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { MatchesService } from 'src/app/services/matches.service';
import { Location } from '@angular/common'

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {


  public wordNumber: number = 5;
  public playerNumber: number = 2;
  public allWords: boolean = true;

  public matchname: string = '';

  constructor(
    private matchesService: MatchesService,
    private router: Router,
    private location: Location
  ) { }

  async ngOnInit() {
    
  }

  back(): void {
    this.location.back()
  }

  async createMatch() {
    const result = await this.matchesService.createNewMatch(this.matchname,this.playerNumber,this.wordNumber, this.allWords);
    if (result) {
      this.router.navigate([`game`],{ queryParams: { match: this.matchname } })
    }
  }

}

