import { Injectable } from '@angular/core';
import { Apollo, gql, QueryRef } from 'apollo-angular';
import { OtherWords, wordleWords } from 'src/assets/eng-word5';
import { addSeconds } from 'date-fns'

@Injectable({
  providedIn: 'root'
})
export class MatchesService {

  public static MAXTRY = 6;
  public STARTROUNDDELAYSECONDS = 10;


  constructor(private apollo: Apollo) {

  }


  async addPlayerToMatch(matchName: string, playerName: string): Promise<any> {

    const result = await this.apollo.mutate({
      mutation: gql`mutation CreatePlayer($playerName:String!,$matchName:String!) {
        createPlayer(
          data: {name: $playerName, match: {connect: {name: $matchName}}, points: 0}
        ) {
          id
        }
      }
      `, variables: {
        playerName,
        matchName
      }
    }).toPromise();
    return result;
  }

  async setRoundStartTime(roundId: string): Promise<any> {
    let startDateTime = new Date();
    startDateTime = addSeconds(startDateTime, this.STARTROUNDDELAYSECONDS)

    const result = await this.apollo.mutate({
      mutation: gql`mutation updateRoundStartTime($startTime: DateTime, $roundId: ID!) {
        updateRound(data: {startTime: $startTime}, where: {id: $roundId}) {
          id
        }
      }`,
      variables: {
        startTime: startDateTime.toISOString(),
        roundId
      }
    }).toPromise();
    return result;
  }
  async getMatch(matchName: string): Promise<any> {
    const result: any = await this.apollo.watchQuery({
      query: gql`query GetMatch($matchName: String!) {
        match(where: {name: $matchName}) {
          id
          playerNumber
          players {
            ... on Player {
              id
              name
              points
            }
          }
          roundnumber
          rounds {
            ... on Round {
              id
              word
              startTime
              answers {
                ... on Asnwer {
                  id
                  attempt
                  complete
                  result
                  value
                  createdAt
                  player {
                    name
                  }
                }
              }
              order
            }
          }
        }
      }
      `, variables: {
        matchName
      }
    }).refetch();
    return result.data.match;
  }


  async createNewMatch(matchName: string, playerNumber: number, wordNumber: number, completeWordPool = false): Promise<any> {
    const roundsWords: string[] = this.createRounds(wordNumber, completeWordPool);

    const roundArray: any[] = []; let i = 1;
    roundsWords.forEach(word => {
      roundArray.push(
        {
          "Round":
            { "order": i, "word": word }
        });
      i++;
    })

    const createMatch = await this.apollo.mutate({
      mutation: gql`mutation newMatch($name: String!, $playerNumber: Int!, $roundnumber: Int!, $rounds: MatchRoundsCreateManyInlineInput!) {
        createMatch(
          data: {name: $name, playerNumber: $playerNumber, roundnumber: $roundnumber, rounds: $rounds}
        ) {
          id
        }
      }
      `, variables:
      {
        "rounds": {
          "create": roundArray
        }, "name": matchName, "playerNumber": playerNumber, "roundnumber": wordNumber
      }
    }).toPromise();

    return createMatch;

  }

  async addAnswer(roundId: string, playerId: string, answerString: string, answerValue: any, attempt: number, isCorrect: boolean): Promise<any> {
    const result = await this.apollo.mutate({
      mutation: gql`mutation addAnswer($attempt: Int!, $complete: Boolean!, $playerId: ID!, $result: Json!, $roundId: ID!, $value: String!) {
        createAsnwer(
          data: {attempt: $attempt, complete: $complete, player: {connect: {id: $playerId}}, result: $result, value: $value, round: {connect: {id: $roundId}}}
        ) {
          id
        }
      }
      `, variables: { "attempt": attempt, "complete": isCorrect, "playerId": playerId, "result": answerValue, "roundId": roundId, "value": answerString }
    }).toPromise();
    return result;
  }

  createRounds(wordNumber: number, completeWordPool: boolean): string[] {
    let saveCounter = 0;
    const res: string[] = [];
    const maxIndex = completeWordPool ? wordleWords.length : OtherWords.length + wordleWords.length;
    while (res.length < wordNumber && saveCounter < 100) {
      const randomIndex = Math.round(Math.random() * (maxIndex - 1));
      let word = '';
      if (randomIndex >= wordleWords.length) {
        word = OtherWords[randomIndex - wordleWords.length]
      } else {
        word = wordleWords[randomIndex]
      }
      if (!res.includes(word)) {
        res.push(word);
      }
      saveCounter++;
    }
    return res;

  }

}
