import { Component, Input, OnInit } from '@angular/core';
import { Answer, Player, Round, Score } from 'src/app/models/match';
import { MatchesService } from 'src/app/services/matches.service';
import { RoundService } from 'src/app/services/round.service';

@Component({
  selector: 'app-gameboard',
  templateUrl: './gameboard.component.html',
  styleUrls: ['./gameboard.component.scss']
})
export class GameboardComponent implements OnInit {

  public round: Round;
  public player: Player;
  public currentWord: string = '     ';
  @Input('round') set setRound(value: Round) {
    this.round = value;
    this.update();
  };
  @Input('player') set setPlayer(value: Player) {
    this.player = value;
    this.update();
  };
  @Input('currentWord') set setCurrentWord(value: string) {
    let word = value;
    if (this.round?.word) {
      while (word.length < this.round.word.length) {
        word += ' ';
      }
    }
    this.currentWord = word;
    this.update();
  };

  public answers: Answer[] = [];
  public currentTry = 1;

  private maxTry = MatchesService.MAXTRY;

  constructor() { }

  ngOnInit(): void {


  }

  update() {
    this.generateAnswer();
  }

  generateAnswer() {
    this.answers = [];
    console.log("------- ~ GameboardComponent ~ generateAnswer ~ this.round", this.round);
    for (let i = 1; i <= this.maxTry; i++) {

      let answered = null;
      if (this.round) { 
        answered = this.round.answers.find(x => x.player.name == this.player.name && x.attempt == i); 
        console.log("------- ~ GameboardComponent ~ generateAnswer ~ answered", answered);
      }
      if (answered) {
        this.answers.push(answered);
        this.currentTry = i + 1;
      } else {
        this.answers.push(
          {
            id: '',
            player: this.player,
            value: i == this.currentTry ? this.currentWord : '     ',
            result: { chars: [2, 2, 2, 2, 2] },
            attempt: i,
            complete: false
          }
        )
      }
    }
  }

}
