import { Injectable } from '@angular/core';
import { Answer, Match, Player, Round, Score } from '../models/match';
import { OtherWords, wordleWords } from 'src/assets/eng-word5';
import { MatchesService } from './matches.service';
import { parseISO, differenceInMilliseconds } from 'date-fns'

@Injectable({
  providedIn: 'root'
})
export class RoundService {

  private maxTry = MatchesService.MAXTRY;
  private POINTSSAMEANSWERDIFF: number = 1;
  private POINTSDIFFERENTANSWERDIFF: number = 2;
  private MAXPOINTSFACTOR: number = 2;

  constructor(
    private matchService: MatchesService
  ) { }

  public isWordAcceptable(round: Round, word: string) {
    // TODO check round type
    return OtherWords.includes(word) || wordleWords.includes(word);
  }

  async sendAnswer(round: Round, answer: string, player: Player): Promise<any> {
    const attempt: number = round.answers.filter(x => x.player.name == player.name).length + 1;
    const result = this.getResult(round, answer);
    return this.matchService.addAnswer(round.id, player.id, answer, result, attempt, answer == round.word);
  }

  getCurrentRound(match: Match): Round | undefined {
    // match.rounds.sort((a, b) => a.order - b.order);
    const nextRound: Round | undefined = match.rounds.find(round => !this.isRoundComplete(round, match));
    if (nextRound && !nextRound.startTime) {
      this.matchService.setRoundStartTime(nextRound.id);
    }
    return nextRound;
  }

  getResult(round: Round, answer: string) {
    const chars: number[] = [];
    answer.split('').forEach((letter, idx) => {
      let val = 0;
      if (round.word.includes(letter)) { val = -1 };
      if (round.word[idx] == letter) { val = 1 };
      chars.push(val);
    });
    return { chars };
  }

  isRoundComplete(round: Round, match: Match): boolean {
    let isComplete = true;
    match.players.forEach(player => {
      isComplete = isComplete && !!round.answers.find(answer => (answer.complete || answer.attempt == this.maxTry) && answer.player.name == player.name);
    })
    return isComplete;
  }

  getMatchLeaderboard(match: Match): Score[] {
    const matchScores: Score[] = [];
    match.players.forEach(player => matchScores.push({ player: player, points: 0, answers: 0, timeSec: 0 }));
    match.rounds.forEach(round => {
      if (this.isRoundComplete(round, match)) {
        const roundScores = this.getRoundLeaderBoard(round, match);
        roundScores.forEach(score => {
          console.log("------- ~ RoundService ~ getMatchLeaderboard ~ score", score);
          const matchScore = matchScores.find(x => x.player.name == score.player.name);
          if (matchScore) {
            matchScore.answers += score.answers;
            matchScore.timeSec += score.timeSec;
            matchScore.points += score.points;
          }
        })
      }
    })
    return matchScores;
  }

  getRoundLeaderBoard(round: Round, match: Match): Score[] {
    const roundScores: Score[] = [];
    match.players.forEach(player => roundScores.push({ player: player, points: 0, answers: this.maxTry + 1, timeSec: 0 }));
    round.answers.forEach(answer => {
      if (answer.complete || answer.attempt == this.maxTry) {
        let score = roundScores.find(x => x.player.name == answer.player.name);
        if (score) {
          score.answers = answer.complete ? answer.attempt : this.maxTry + 1;
          score.timeSec = this.getTimeElapsed(answer, round);
        }
      }
    })

    roundScores.sort((a, b) => {
      if (a.answers != b.answers) { return a.answers - b.answers }
      return a.timeSec - b.timeSec;
    })

    let lastPoint = this.getMaxPoints(match);
    let lastAnswerNum = 0;
    let lastTimeSec = 0;
    console.log("------- ~ RoundService ~ getRoundLeaderBoard ~ roundScores", roundScores);
    roundScores.forEach(x => {
      if (lastAnswerNum != 0 || lastTimeSec != 0) {
        if (lastAnswerNum == x.answers) {
          if (x.timeSec > lastTimeSec) {
            lastPoint -= this.POINTSSAMEANSWERDIFF;
          }
        } else {
          lastPoint -= this.POINTSDIFFERENTANSWERDIFF;
        }
      }
      x.points = lastPoint;
      lastTimeSec = x.timeSec;
      lastAnswerNum = x.answers;
    })
    console.log("+++++++ ~ RoundService ~ getRoundLeaderBoard ~ roundScores", roundScores);

    return roundScores;
  }
  getMaxPoints(match: Match) {
    return match.playerNumber * this.MAXPOINTSFACTOR;
  }

  getTimeElapsed(answer: Answer, round: Round): number {
    let diff = 0;
    if (answer.createdAt) {
      diff = differenceInMilliseconds(parseISO(answer.createdAt), parseISO(round.startTime));
    }

    return diff; // TODO computer time of the answer
  }
}


